<template>
    <div>

    </div>
</template>

<script>
    export default {
        data () {
            return {}
        },
        methods: {
          getUrlParams (name) {
            let reg = new RegExp("(^|&)"+ name +"=([^&]*)(&|$)");
            let r = window.location.search.substr(1).match(reg);
            if(r!=null)return  unescape(r[2]); return null;
          }
        },
        mounted(){
          let code = this.getUrlParams('code')
          if(code){
            this.post('/wxapi/Apioauth/getToken',{code:code}).then(res => {
		      	console.log(res)
		        if (res.data.code === 10000) {
//		          location.href = 'http://nhsz.xiguawenhua.com/shop/#/'
		          this.$router.push({
		          	name:'Index'
		          })
		          sessionStorage.setItem('token',res.data.token)
		        }else if(res.data.code === 10003){
		        	alert(res.data.data)
		        }
		      })
          }else{
//            this.$router.push({name:'Login'})
          }
        },
        components: {}
    }
</script>

<style scope>

</style>



